<template>
  <div class="sidebar">
    <el-menu
      class="sidebar-el-menu"
      :default-active="onRoutes"
      :collapse="collapse"
      background-color="#303133"
      text-color="#fff"
      active-text-color="orange"
      unique-opened
      router
    >
      <template v-for="item in items">
        <template v-if="item.subs">
          <el-submenu :index="item.index" :key="item.index">
            <template slot="title">
              <i :class="item.icon"></i>
              <span slot="title">{{ item.title }}</span>
            </template>
            <template v-for="subItem in item.subs">
              <el-submenu
                v-if="subItem.subs"
                :index="subItem.index"
                :key="subItem.index"

              >
                <template slot="title">{{ subItem.title }}</template>
                <el-menu-item
                  v-for="(threeItem,i) in subItem.subs"
                  :key="i"
                  :index="threeItem.index"
                >
                  {{ threeItem.title }}
                </el-menu-item>
              </el-submenu>
              <el-menu-item
                v-else
                :index="subItem.index"
                :key="subItem.index"
              >{{ subItem.title }}</el-menu-item>
            </template>
          </el-submenu>
        </template>
        <template v-else>
          <el-menu-item :index="item.index" :key="item.index">
            <i :class="item.icon"></i>
            <span slot="title">{{ item.title }}</span>
          </el-menu-item>
        </template>
      </template>
    </el-menu>
  </div>
</template>

<script>
import store from "../../store/store";
import bus from './bus';
export default {
  data() {
    return {
      collapse: false,
      items: [
        {
          icon: 'el-icon-s-home',
          index: 'homepage',
          title: '系统首页',
          meta:{
            permissions:['super_admin','hr','部门主管']
          }
        },
        {
          icon: 'el-icon-search',
          index: 'infSearch',
          title: '信息查询',
          meta: {
            permissions:['hr','部门主管']
          },
          subs:[
            {
              index: 'employeeSearch',
              title: '员工查询',
            },
            {
              index: 'personnelSearch',
              title: '人才查询',
            },
          ]
        },
        {
          icon: 'el-icon-document-add',
          index: 'informationEntry',
          title: '信息录入',
          meta: {
            permissions:['hr']
          },
          subs:[
            {
              index:'employeeFile',
              title:'员工档案'
            },
            // {
            //   index:'employeeComments',
            //   title:'员工评价'
            // },
          ]
        },
        {
          icon: 'el-icon-s-custom',
          index: 'infCheck',
          title: '信息审核',
          meta: {
            permissions:["super_admin"]
          },
          subs:[
            {
              index:'userInfCheck',
              title:'待审核信息'
            },
            {
              index:'allUsers',
              title:'所有用户'
            },
          ]
        },
      ],
      item:[]
    };
  },
  computed: {
    onRoutes() {
      return this.$route.path.replace('/', '');
    }
  },
  created() {
    // 通过 Event Bus 进行组件间通信，来折叠侧边栏
    bus.$on('collapse', msg => {
      this.collapse = msg;
      bus.$emit('collapse-content', msg);
    });
  },
  mounted() {
    this.checkPermission()
  },
  methods:{
    checkPermission(){
      for (let i=0;i<this.items.length;i++){
        this.items[i]=JSON.parse(JSON.stringify(this.items[i]))
      }
      this.item=this.items.filter((item)=>{
        for (let i=0;i<item.meta.permissions.length;i++){
          let inf=localStorage.getItem('role')
          inf = inf.replace("\"","").replace("\"","");
          if (item.meta.permissions[i]==inf){//筛选权限
            return item
            break;
          }
        }
      })
      this.items=this.item
    }
  }
};
</script>

<style scoped>
.sidebar {
  display: block;
  position: absolute;
  left: 0;
  top: 70px;
  bottom: 0;
  overflow-y: scroll;
}
.sidebar::-webkit-scrollbar {
  width: 0;
}
.sidebar-el-menu:not(.el-menu--collapse) {
  width: 250px;
}
.sidebar > ul {
  height: 100%;
}
</style>



